
.absolute {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

.list-block {
	li {
		@extend .font-sans;
		display: inline-block;
		a {
			display: inline-block;
			@extend .link-gray;
		}
		&:last-child {
			a {
				padding-right: 0;
			}
		}
	}
}
.list-xs {
	font-size: .72rem;
	@extend .list-block;
}
.list-sm {
	font-size: 1rem;
	@extend .list-block;

}


.site-header {
	background-color: $background-color
}
.header-top,
.header-bottom {
	@extend .clearfix;
	
	overflow: hidden;
	li {
		a {
			padding: 0 1rem;
		}
		&.active {
			a {
				color: #333;
			}
		}
	}
}
.header-top {
	li a {
		padding-top: .5rem;
	}
}
.tools-menu {
	position: absolute;
	top: 0;
	right: .625rem;
}
.header-bottom {
	@extend .border-bottom;
	height: 45px;
	li  {
		a {
			line-height: 45px;
		}
	}
}
.header-middle {
	height: 70px;
	padding: ($spacer / 2) 0;
	overflow: hidden;
	@extend .border-bottom;
	line-height: 1;
	li a {
		line-height: 40px;
		padding: 0 1rem;
		margin-top: .4rem;
		i:before {
			transform: scale(1.8);
			margin-right: .3rem;
		}
	}
}
.berlingske-logo {
	background-image: url('data:image/svg+xml;base64,');
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center center;
	overflow: hidden;
}

.site-logo {
	height: calc(80px - 2rem);
	@extend .berlingske-logo;
	width: 20rem;
	display: inline-block;
	margin: 0;
	text-indent: -9999px;
	transition: all .2s ease;

}

